//------------------------------------------------------------
// = TYPOGRAPHY
//------------------------------------------------------------

@import url('https://fonts.googleapis.com/css?family=Roboto:100,300,400,500,700,900&display=swap&subset=latin-ext');


@font-face {
  font-family: 'GESSTwoMedium-Medium';
  src: url('../font/GESSTwoMedium-Medium.eot?#iefix') format('embedded-opentype'),
  url('../font/GESSTwoMedium-Medium.otf')  format('opentype'),
  url('../font/GESSTwoMedium-Medium.woff') format('woff'),
  url('../font/GESSTwoMedium-Medium.ttf')  format('truetype'),
  url('../font/GESSTwoMedium-Medium.svg#GESSTwoMedium-Medium') format('svg');
  font-weight: normal;
  font-style: normal;
}

@mixin f-gesstMedium() {
  font-family: $font-3;
  font-weight: normal;
}

@mixin f-roboto($font-weight) {
  font-family: $font-2;
  font-weight: $font-weight;
}

@mixin fontsize($size:1, $line: $size * 1.4) {
  font-size: $size + rem;
  line-height: $line + rem;
}

%fs-42{
  @include fontsize(4.2);
}
%fs-36{
  @include fontsize(3.6);
}
%fs-32{
  @include fontsize(3.2);
}
%fs-25{
  @include fontsize(2.5);
}
%fs-24{
  @include fontsize(2.4);
}
%fs-22{
  @include fontsize(2.2);
}
%fs-18{
  @include fontsize(1.8);
}
%fs-16{
  @include fontsize(1.6);
}
%fs-14{
  @include fontsize(1.4);
}
%fs-13{
  @include fontsize(1.3);
}
%fs-12{
