//------------------------------------------------------------
// = BUTTONS
//------------------------------------------------------------

.button{
  display: inline-block;
  text-align: center;
  border: none;
  outline: none !important;
  &.full-width{
    width: 100%;
  }
  &.fix-size-sml{
    width: 150px;
  }
  &.button-icons{
    i{
      margin-right: 10px;
      font-size: 16px;
    }
  }
  &.sign-out{
    i{
      display: inline-block;
      transform: rotate(180deg);
    }
  }
}

.button-v1{
  @extend %fs-16;
  @include f-roboto(700);
  height: 55px;
  line-height: 55px;
  color: $color-white;
  background: $color-1;
  border-radius: 10px;
  transition: $transition;
  &:hover{
    color: $color-white;
    background: $color-1;
  }
  &.color-1{
    color: $color-white;
    background: $color-1;
    &:hover{
      color: $color-white;
      background: $color-1;
    }
  }
  &.color-2{
    color: $color-white;
    background: $color-4;
    &:hover{
      color: $color-white;
      background: $color-4;
    }
  }
  &.color-3{
    color: $color-white;
    background: $color-7;
    &:hover{
      color: $color-white;

      background: $color-7;
    }
  }
}
.button-v2{
  @extend %fs-16;
  @include f-roboto(400);
  height: 50px;
  line-height: 50px;
  color: $color-1;
  background: $color-white;
  border-radius: 10px;
  padding: 0 20px;
}
.button-v3{
  @extend %fs-16;
  @include f-roboto(400);
  display: inline-block;
  height: 50px;
  line-height: 50px;
  color: $color-black;
  background: transparent;
  border: 1px solid rgba($color-black, 0.3);
  border-radius: 10px;
}
.button-v4{
  @extend %fs-16;
  @include f-roboto(400);
  display: inline-block;
  height: 50px;
  line-height: 50px;
  text-align: center;
  border-radius: 4px;
  padding: 0 20px;
  &.line-height-sml{
    height: 30px;
    line-height: 30px;
  }
  &.sml-icon-btn{
    display: block;
    width: 28px;
    height: 28px;
    line-height: 28px;
    padding: 0;
    &.color-1{
      font-size: 12px;
    }
    &.color-2{
      font-size: 22px;}
  }
  &.icon-btn-with-text-v1{
    i{
      margin-left: 10px;
    }
  }
  &.icon-btn-with-text-v2{
    i{
      margin-left: 10px;
    }
  }
  &.color-1{
    color: $color-1;
    background-color: rgba($color-1,0.15);
  }
  &.color-2{
    color: $color-4;
    background-color: rgba($color-4,0.15);
  }
  &.color-3{
    color: $color-white;
    background-color: $color-1;
  }
  &.color-4{
    color: $color-white;
    background-color:$color-4;
  }
  &.color-5{
    color: $color-white;
    background: $color-7;
    &:hover{
      color: $color-white;
      background-color: rgba($color-7,0.9);
    }
  }
}
.button-v5{
  @extend %fs-16;
  @include f-roboto(400);
  display: inline-block;
  height: 50px;
  line-height: 50px;
  background-color: rgba($color-white,0.3);
  color: $color-black;
  border-radius: 8px;
  padding: 0 20px;
  margin-right: 32px;
  span{
    color: rgba($color-black,0.7);
  }
}
.button-v6{
  @extend %fs-16;
  @include f-roboto(400);
  display: inline-block;
  height: 50px;
  line-height: 50px;
  text-align: center;
  padding: 0 20px;
  color: $color-1;
  background-color: rgba($color-1, 0.15);
  &:hover{
    color: $color-white;
    background-color: $color-1;
  }
}

.button-v7{
  @extend %fs-16;
  @include f-roboto(normal);
  display: inline-block;
  margin:auto;
  width: 80%;
  line-height: 40px;
  text-align: center;
  border: none;
  border-radius: 4px;
  padding: 0 7px;
  transition: $transition;
  outline: none;
  &.color-1{
    color: $color-white;
    background-color: $color-8;
    &:hover{
      color: $color-white;
      background-color: rgba($color-8, 0.80);
      text-decoration: none;
    }
  }
  &.color-2{
    color: $color-white;
    background-color: $color-9;
    &:hover{
      color: $color-white;
      background-color: rgba($color-9, 0.80);
      text-decoration: none;
    }
  }
}

